<template>
<div class="container">
  <div class="columns is-mobile">
      <div class="column is-6">
        <p class="control is-expanded">
          <input v-model="items[idx]" type="number" class="input"/>
        </p>
      </div>
      <div class="column has-text-right sp-price-with-tax">
        <span>{{ priceWithTax(items[idx]) }}</span>
      </div>
      <div class="column is-1">
        <button class="button is-primary" @click="$emit('remove')">-</button>
      </div>
  </div>

</div>
</template>

<script>
export default {
  name: 'item-list',
  props: ['items', 'idx'],
  methods: {
    priceWithTax (price) {
      const formatter = new Intl.NumberFormat('ja-JP')
      return formatter.format(Math.floor(price * 1.08))
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../node_modules/bulma/bulma.sass";
.sp-price-with-tax{
  margin-top: 20px;
}
</style>



